import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import { useSpring, animated } from "react-spring/web.cjs"; // web.cjs is required for IE 11 support
import "bootstrap/dist/css/bootstrap.min.css";
import "./style.css";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

const Fade = React.forwardRef(function Fade(props, ref) {
  const { in: open, children, onEnter, onExited, ...other } = props;
  const style = useSpring({
    from: { opacity: 0 },
    to: { opacity: open ? 1 : 0 },
    onStart: () => {
      if (open && onEnter) {
        onEnter();
      }
    },
    onRest: () => {
      if (!open && onExited) {
        onExited();
      }
    },
  });

  return (
    <animated.div ref={ref} style={style} {...other}>
      {children}
    </animated.div>
  );
});

Fade.propTypes = {
  children: PropTypes.element,
  in: PropTypes.bool.isRequired,
  onEnter: PropTypes.func,
  onExited: PropTypes.func,
};

export default function SpringModal(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <button type="button" className="btn btn-info" onClick={handleOpen}>
        More Info...
      </button>
      <Modal
        aria-labelledby="spring-modal-title"
        aria-describedby="spring-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className="card" id="pop-up-card">
            <img id="profile-img-large" alt={props.name} src={props.image} />
            <div className="card-body">
              <h5 className="card-title">{props.name}</h5>
            </div>
            <ul className="list-group list-group-flush">
              <li className="list-group-item">
                <strong>Job:</strong> {props.occupation}
              </li>
              <li className="list-group-item">
                <strong>Age:</strong> {props.age}
              </li>
              <li className="list-group-item">
                <strong>Experience:</strong> {props.experience}
              </li>
              <li className="list-group-item">
                <strong>Phone Number:</strong> {props.phone}
              </li>
              <li className="list-group-item">
                <strong>Email Address:</strong> {props.email}
              </li>
              <li className="list-group-item">
                <strong>Location:</strong> {props.location}
              </li>
            </ul>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
